.search-container {

    display: flex;
 justify-content: center;
 margin: 10px;

}

.search-input {

   padding: 8px 15px ;
   font-size: 15px ;
   width: 250px;
   border-width: 0px;

   background: #0592f2;
   color: #e7e9f0;
   border-radius: 50px;

}

.search-input::placeholder {

    color: #e7e9f0;
}
.search-input:focus {

    outline: none;
}





